<!--预警状态同比环比-->
<template>
  <div
    class="yjzt-wrapper"
    v-loading="!data.length"
    :element-loading-background="$STANDARD.loadingBgColor">
    <div class="part part1">
      <p class="label">{{data[0].name}}</p>
      <div :class="['btn',btnColor]">{{data[0].value}}</div>
    </div>
    <div class="part part2">
      <p class="label">{{data[1].name}}</p>
      <allCount class="all-count" :count="data[1].value"></allCount>
    </div>
    <div class="part part3">
      <p class="label">{{data[2].name}}</p>
      <div class="inner">
        <span class="per">{{(data[2].value*100).toFixed(2)}}%</span>
        <i :class="['icon',data[2].value>0?'up-icon':'down-icon']"></i>
      </div>
    </div>
    <div class="part part4">
      <p class="label">{{data[3].name}}</p>
      <div class="inner">
        <span class="per">{{(data[3].value*100).toFixed(2)}}%</span>
        <i :class="['icon',data[3].value>0?'up-icon':'down-icon']"></i>
      </div>
    </div>
  </div>
</template>

<script>
  import allCount from "@/components/list/allCount";

  export default {
    name: "index",
    components: {
      allCount,
    },
    props: {
      data: {
        type: Array,
        default(){
          return [
            {
              "name": "预警状态",
              "value": "告警"
            },
            {
              "name": "未来一周信访量",
              "value": -1
            },
            {
              "name": "同比",
              "value": 0
            },
            {
              "name": "环比",
              "value": 0
            }
          ]
        }
      }
    },

    computed: {
      btnColor() {
        if (this.data[0].value === '平稳') {
          return 'btn-blue'
        } else if (this.data[0].value === '异常') {
          return 'btn-yellow'
        } else if (this.data[0].value === '告警') {
          return 'btn-red'
        }
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .yjzt-wrapper
    position relative
    display flex
    justify-content center
    align-items center
    width 100%
    height 100%
    padding 0 .5rem
    .part
      border-right 1px dashed #91e3f7
      .label
        text-align center
        font-size .24rem
        line-height .7rem
        color #fff
        margin-bottom .2rem
      .inner
        display flex
        justify-content center
        align-items center
        .per
          font-size .16rem
          color #c7f6fa
          line-height .4rem
        .icon
          display inline-block
          width .4rem
          height .4rem
          margin 0.05rem 0
          &.up-icon
            background url("./img/up-icon.png") no-repeat
          &.down-icon
            background url("./img/down-icon.png") no-repeat
      &:last-child
        border-right none
    .part1
      flex 1.5
      .btn
        width .6rem
        line-height .3rem
        text-align center
        font-size .14rem
        border-radius 4px
        margin 0.1rem auto
        &.btn-blue
          color #fff
          background-color #0b598e
          border 2px solid #14a4f1
        &.btn-yellow
          color #f9db3b
          background-color #765b31
          border 2px solid #ffb435
        &.btn-red
          color #e9ebfc
          background-color #6b2f40
          border 2px solid #e95659
    .part2
      flex 2
      .all-count
        position relative
        top -0.1rem
    .part3
      flex 1
    .part4
      flex 1
</style>
